.wp-block-nortic-plugin-rating {
  background-color: #ffffff;
  border: 1px solid rgb(238, 238, 238);
  padding: 2rem 4rem;
  border-radius: 0.5rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.wp-block-nortic-plugin-rating .nortic-plugin-rating-header {
  text-align: center;
}
.wp-block-nortic-plugin-rating .nortic-plugin-rating-header f5 {
  font-size: 1.62rem;
  font-weight: 700;
  color: #003876;
}

.wp-block-nortic-plugin-rating .post-type-rating-data {
  text-align: center;
  margin-top: 2rem;
}
.wp-block-nortic-plugin-rating .post-type-rating-meta {
  margin-top: 1rem;
}
.wp-block-nortic-plugin-rating
  .post-type-rating-meta
  .post-type-rating-meta-bar-value,
.wp-block-nortic-plugin-rating
  .post-type-rating-meta
  .post-type-rating-meta-label {
  font-size: 0.75rem;
}
.wp-block-nortic-plugin-rating
  .post-type-rating-meta
  .post-type-rating-meta-label {
  color: #003876;
}
.wp-block-nortic-plugin-rating
  .post-type-rating-meta
  .post-type-rating-meta-bar-value {
  color: #515151;
}
.wp-block-nortic-plugin-rating
  .post-type-rating-meta
  div.post-type-rating-meta-bar-wrap {
  height: 0.5rem;
  background-color: #e5e7eb;
}
.wp-block-nortic-plugin-rating
  .post-type-rating-meta
  div.post-type-rating-meta-bar-wrap
  div.post-type-rating-meta-bar {
  height: 0.5rem;
  background-color: #faaf00;
}
.wp-block-nortic-plugin-rating ul.editor-rating-preview li i {
  color: #faaf00;
}
